header {
	position: relative;
	height: 50px; }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: $color-black;
	z-index: 99; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none;
	&__link {
		color: $color-white;
		font-size: 15px;
		font-weight: 300;
		text-decoration: none; }
	&__item {
		border-right: 2px dotted black;
		padding-right: 28px;
		&:last-child {
			border-right: none; } } }

.subheader {
	position: absolute;
	top: 50px;
	width: 100%;
	height: 84px;
	background-color: rgba(0,0,0, .6);
	&__logo {
		display: block;
		max-width: 100%;
		margin-top: 16px; }
	&__official {
		margin-top: 6px;
		opacity: 0.5;
		color: $color-white;
		font-size: 13px;
		font-weight: 400; }
	&__call {
		color: $color-white;
		font-size: 12px;
		font-weight: 300;
		margin-top: 20px;
		text-align: right; }
	&__phone {
		display: block;
		margin-top: 8px;
		color: $color-white;
		font-size: 22px;
		font-weight: 400;
		text-transform: uppercase;
		text-align: right;
		line-height: 12px;
		text-decoration: none; }
	&__btn {
		display: flex;
		flex-direction: column;
		justify-content: center;
		margin-top: 21px;
		width: 172px;
		height: 46px;
		background-image: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border-radius: 4px;
		border: none;
		font-size: 14px;
		font-weight: 400;
		text-align: center;
		text-transform: uppercase;
		color: $color-white;
		cursor: pointer;
		@include btn_hover; } }
.promo {
	min-height: 900px;
	background: url('../img/uber_bg.png') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	margin-bottom: 20px;
	&__header {
		color: $color-white;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 60px;
		line-height: 60px;
		margin: 0;
		text-align: center; }
	&__subheader {
		color: $color-white;
		font-weight: 700;
		text-transform: uppercase;
		font-size: 36px;
		line-height: 36px;
		margin: 0;
		margin-top: 22px;
		text-align: center; }
	&__descr {
		color: $color-white;
		font-size: 18px;
		font-weight: 400;
		line-height: 24px;
		text-align: center;
		margin-top: 23px; }
	&__btn {
		display: block;
		margin: 118px auto 0 auto;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: $color-white;
		font-size: 18px;
		font-weight: 400;
		text-transform: uppercase;
		@include btn_hover; } }

.reasons {
	margin-bottom: 64px;
	&__block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }
	&__round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 116px;
		height: 116px;
		background-color: $color-blue;
		border-radius: 100%; }
	&__descr {
		width: 343px; }
	&__subtitle {
		color: $color-blue;
		font-size: 17px;
		font-weight: 400;
		line-height: 21px; }
	&__text {
		color: $color-black;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px; } }
.label {
	position: relative;
	margin: 0 auto;
	display: flex;
	width: 113px;
	height: 24px;
	flex-direction: column;
	justify-content: center;
	background: $color-black;
	color: $color-white;
	font-size: 11px;
	font-weight: 400;
	text-align: center;
	text-transform: uppercase;
	z-index: 9;
	&__white {
		background-color: $color-white;
		color: $color-black; }
	&__min {
		width: 75px; } }
.title {
	position: relative;
	margin-bottom: 0;
	color: $color-black;
	text-align: center;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	margin-top: 33px;
	z-index: 9;
	&__white {
		color: $color-white; } }
.subtitle {
	position: relative;
	color: $color-black;
	text-align: center;
	font-size: 17px;
	font-weight: 400;
	z-index: 9;
	&__white {
		color: $color-white; } }


.mobile {
	min-height: 864px;
	padding: 48px 0 80px 0;
	background: url('../img/mobile_bg.png')	center center/cover no-repeat;
	.row {
		margin-top: 30px; }
	&__item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 82px;
		&_1 {
			background: url('../img/Карточка 1.png') center center/cover no-repeat; }
		&_2 {
			background: url('../img/Карточка 2.png') center center/cover no-repeat; }
		&_3 {
			background: url('../img/Карточка 3.png') center center/cover no-repeat; }
		&_4 {
			background: url('../img/Карточка 4.png') center center/cover no-repeat; }
		&_5 {
			background: url('../img/Карточка 5.png') center center/cover no-repeat; }
		&_6 {
			background: url('../img/Карточка 6.png') center center/cover no-repeat; }
		&_subtitle {
			color: $color-white;
			text-align: center;
			font-size: 24px;
			font-weight: 400;
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translate(-50%);
			width: 26px;
			height: 26px;
			background-color: $color-black;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&::before {
				content: "";
				display: block;
				width: 14px;
				height: 2px;
				background-color: $color-blue;
				transition: 0.3s all; }
			&::after {
				content: "";
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				width: 2px;
				height: 14px;
				background-color: $color-blue;
				transition: 0.3s all; }
			&:hover {
				&::before, &::after {
					background-color: $color-red; } } } } }



.choice {
	position: relative;
	min-height: 895px;
	padding: 57px 0 138px 0;
	background: url('../img/mobile_bg.png') center center/cover no-repeat;
	text-align: center;
	img {
		position: relative;
		display: block;
		margin: 56px auto 0 auto;
		z-index: 9; }
	&__descr {
		text-align: center;
		color: $color-black;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.black {
		position: absolute;
		top: 0;
		left: 0;
		height: 499px;
		width: 100%;
		background-color: $color-black;
		z-index: 3; } }


.require {
	padding: 68px 0 90px 0;
	&__block {
		margin: 33px 0;
		&_nmb {
			margin-bottom: 0px; } }
	&__title {
		color: $color-blue;
		font-size: 22px;
		font-style: normal;
		font-weight: 400;
		margin-bottom: 20px; }
	&__list {
		list-style-type: none;
		padding-left: 50px;
		li {
			position: relative;
			color: $color-black;
			font-weight: 300;
			font-size: 17px;
			line-height: 24px;
			margin-bottom: 10px;
			&:before {
				content: "";
				display: block;
				position: absolute;
				left: -32px;
				top: 3px;
				width: 21px;
				height: 20px;
				background: url('../icons/iconsblue.svg') center center/cover no-repeat; } } }
	&__descr {
		color: $color-blue;
		font-size: 17px;
		font-weight: 300;
		line-height: 24px; }
	.warning {
		.require__title {
			color: $color-red; }
		.require__list {
			li {
				&:before {
					background: url('../icons/iconsred.svg') center center/cover no-repeat; } } } } }
.cities {
	position: relative;
	padding: 48px 0;
	background: url('../img/world_bg.png');
	min-height: 720px;
	&__link {
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		bottom: 49px;
		text-align: center;
		font-size: 15px;
		font-weight: 400;
		line-height: 68px;
		text-transform: uppercase;
		color: $color-blue;
		text-decoration: none;
		&:hover {
			text-decoration: none;
			color: $color-white; } } }

footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&__divider {
		width: 299px;
		height: 4px;
		background-color: $color-blue;
		margin: 0 auto; }
	&__wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&__wrapper > div {
		width: 33%;
		&:nth-child(3) {
			display: flex;
			justify-content: flex-end; } }
	&__social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: $color-black;
			border-radius: 100%;
			text-decoration: none;
			i {
				color: $color-white; } } }

	&__links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: $color-black;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase;
				text-decoration: none; } }
		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase;
				text-decoration: none; } }
		&_lang {
			display: block;
			margin: 0 auto;
			color: $color-blue;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center;
			text-decoration: none; } }

	&__mobile {
		a {
			text-decoration: none; } } }
