        &__descr {
            width: 300px; } }
    .mobile {
        &__item {
            width: 300px; } }
    .require {
        br {
            display: none; } }
    .footer {
        &__wrapper > div {
            width: 50%;
            &:nth-child(1) {
                width: 30%; }
            &:nth-child(3) {} } }
				width: 25%;
